<template>
  <div class="addressedit">
    <hx-Return :title="title"></hx-Return>

    <van-address-edit
      :area-list="areaList"
      show-postal
      show-delete
      show-set-default
      show-search-result
      :search-result="searchResult"
      :area-columns-placeholder="['请选择', '请选择', '请选择']"
      :address-info="addressInfo"
      @save="onSave"
      @delete="onDelete"
    />
  </div>
</template>

<script>
import hxReturn from "../../components/hxReturn";
import areaList from "../../assets/erea";

import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("address");

export default {
  data() {
    return {
      title: "修改地址",
      areaList,
      index:"",
      searchResult: [],
      addressInfo:{},
    };
  },
  methods: {
    ...mapMutations(["add"]),
    onSave({ name, tel, province, city, county, areaCode, addressDetail ,isDefault}) {
      this.$store.commit("address/editid", {
        index:this.index,
        name,
        tel,
        address: province + city + county + addressDetail,
        province,
        city,
        county,
        areaCode,
        addressDetail,
        isDefault,
      });
      this.$router.back(-1);
    },
    onDelete() {
      this.$store.commit("address/del", {
        index:this.index,
      });
      this.$router.back(-1);
    }
  },
  computed: {
    ...mapState({
      list: state => state.list
    })
  },

  created() {
    //隐藏导航
    this.$store.state.vantTab = false;
    this.addressInfo = this.$route.query.obj
    this.index = this.$route.query.index
    // console.log(this.addressInfo)
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    this.$store.state.vantTab = true;
  },
  components: {
    hxReturn
  }
};
</script>

<style lang="less" scoped>
.addressedit {
  padding-top: 44px;
}
</style>